.Rocket-details-card {
    width: 18rem;
    padding: 1rem;
    margin-bottom: 15px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.Rocket-details-card:hover{
    box-shadow: 0 8px 16px 0 rgba(100, 87, 87, 0.767);
}
.Rocket-mission-image {
    background-color: #F2F2F2;
}

.Rocket-mission-name-flight-number {
    color: #63659f;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    margin-bottom: 1rem;
}

.Rocket-detail-label {
    color: #181818;
    font-size: 18px;
    font-weight: 500;
}

.Rocket-detail-value {
    color: #63659f;
    font-weight: 400;
}